@media (min-width: 992px) {
  .rounded-lg-3 { border-radius: .3rem; }
}

.bd-placeholder-img {
font-size: 1.125rem;
text-anchor: middle;
-webkit-user-select: none;
-moz-user-select: none;
user-select: none;
}

@media (min-width: 768px) {
.bd-placeholder-img-lg {
  font-size: 3.5rem;
}
}

.b-example-divider {
height: 3rem;
background-color: rgba(0, 0, 0, .1);
border: solid rgba(0, 0, 0, .15);
border-width: 1px 0;
box-shadow: inset 0 .5em 1.5em rgba(0, 0, 0, .1), inset 0 .125em .5em rgba(0, 0, 0, .15);
}

.b-example-vr {
flex-shrink: 0;
width: 1.5rem;
height: 100vh;
}

.bi {
vertical-align: -.125em;
fill: currentColor;
}

.nav-scroller {
position: relative;
z-index: 2;
height: 2.75rem;
overflow-y: hidden;
}

.nav-scroller .nav {
display: flex;
flex-wrap: nowrap;
padding-bottom: 1rem;
margin-top: -1px;
overflow-x: auto;
text-align: center;
white-space: nowrap;
-webkit-overflow-scrolling: touch;
}  

ul#menu li {
display:inline;
}

.fa {  
padding: 20px;  
text-align: center;  
margin: 5px 2px;  
font-size: 30px;  
width: 50px; 
} 

.fa:hover {  
opacity: 0.5;  
} 

.subtopic{
margin-bottom: 40px;
}

.featurette-divider{
margin: 5rem 0;
}

.carousel-control-prev-icon, .carousel-control-next-icon{
  background-color: black;
}